J;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,iBAAiB;IACjB,gBAAgB;;IAEhB,iBAAiB;IACjB,kBAAkB;IAClB,sBAAsB;;IAEtB,SAAS;AACb;;AAEA;EACE,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;EACE,aAAa;EACb,eAAe;EACf,SAAS;EACT,uBAAuB;EACvB,uBAAuB;EACvB,WAAW;AACb;AACA;EACE,aAAa;EACb,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,WAAW;EACX,mBAAmB;EACnB,oCAAoC;AACtC;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,kBAAkB;EAClB,QAAQ;EACR,WAAW;EACX,aAAa;EACb,8BAA8B;EAC9B,2BAA2B;AAC7B;;AAEA;EACE,8BAA8B;EAC9B,YAAY;EACZ,YAAY;EACZ,sBAAsB;EACtB,eAAe;EACf,kBAAkB;EAClB,iBAAiB;AACnB;;AAEA;EACE,iCAAiC;AACnC;;AAEA;EACE,aAAa;EACb,iCAAiC;EACjC,aAAa;EACb,mBAAmB;EACnB,0BAA0B;AAC5B;;AAEA;;EAEE,qBAAqB;AACvB;;AAEA;EACE,mBAAmB;EACnB,mBAAmB;AACrB;;AAEA,YAAY;;AAEZ;IACI,aAAa;IACb,uBAAuB;IACvB,sBAAsB;IACtB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,mCAAmC;IACnC,8BAA8B;IAC9B,4BAA4B;IAC5B,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,UAAU;IACV,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;AACjB;;AAEA,WAAW;;AAEX;;IAEI,WAAW;IACX,aAAa;;AAEjB;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;EACjB,kBAAkB;EAClB,yCAAyC;EACzC,yCAAyC;AAC3C","sourcesContent":["/* GENERAL */\r\n\r\n:root {\r\n    --bg-gradient: linear-gradient(to right, #d8e2ff, #9dadfc, #8a91fc);\r\n    --text-color: #000000;\r\n    --section-bg: white;\r\n    --btn-bg: none;\r\n    --btn-color: black;\r\n}\r\n\r\n*{\r\n    margin:0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\r\n}\r\n\r\n\r\nbody{\r\n    font-family: \"Poppins\", sans-serif;\r\n    background: var(--bg-gradient);\r\n    color: var(--text-color);\r\n    min-height: 100vh;\r\n    padding: 2rem;\r\n    animation: gradientBG 60s ease infinite;\r\n    background-size: 200% 200%;\r\n\r\n    display: flex;\r\n    flex-direction: column;\r\n    min-height: 100vh;\r\n    margin: 0;\r\n}\r\n\r\nbody.dark-mode {\r\n    --bg-gradient: linear-gradient(to right, #1c1c1c, #333, #444);\r\n    --text-color: #f5f5f5;\r\n    --section-bg: #2c2c2c;\r\n    --btn-bg: #444;\r\n    --btn-color: #f5f5f5;\r\n}\r\n\r\n@keyframes gradientBG {\r\n    0% {background-position: 0% 50%;}\r\n    50% {background-position: 100% 50%;}\r\n    100% {background-position: 0% 50%;}\r\n}\r\n\r\nhtml {\r\n    scroll-behavior: smooth;\r\n}\r\n\r\np {\r\n    color: rgb(24, 14, 51);\r\n}\r\n\r\n.text-container p{\r\n    text-indent: 3ch;\r\n    margin-bottom: 1em;\r\n}\r\n\r\nli {\r\n    list-style-type: none;\r\n}\r\n\r\n.theme-toggle {\r\n    background: none;\r\n    border: 2px solid #333;\r\n    border-radius: 8px;\r\n    font-size: 1.2rem;\r\n    padding: 0.5rem 1rem;\r\n    cursor: pointer;\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.theme-toggle,\r\n.lang-toggle {\r\n  position: relative;\r\n  z-index: 15;\r\n}\r\n\r\n.theme-toggle:hover {\r\n    background-color: rgba(0,0,0,0.1);\r\n}\r\n\r\n.lang-toggle {\r\n    font-size: 1rem;\r\n    padding: 0.25rem 0.5rem;\r\n    border-radius: 8px;\r\n    border: 2px solid #333;\r\n    background: none;\r\n    cursor: pointer;\r\n}\r\n\r\n/* TRANSITION */\r\n\r\na, .btn {\r\n    transition: all 300ms ease;\r\n}\r\n\r\n/* DESKTOP NAV*/\r\nnav, .nav-links {\r\n    display: flex;\r\n    gap: 1rem;\r\n    padding: 0.5rem;\r\n    \r\n}\r\n\r\nnav {\r\n    justify-content: space-around;\r\n    color: #000000;\r\n    align-items: center;\r\n    height: 17vh;\r\n    background: rgba(255,255,255,0.05);\r\n    border-radius: 15px;\r\n    backdrop-filter: blur(10px);\r\n    box-shadow: 0 8px rgba(145, 139, 139, 0.1);\r\n}\r\n\r\n.nav-links {\r\n    gap: 2rem;\r\n    list-style: none;\r\n    font-size: 1.5rem;\r\n}\r\n\r\n#desktop-nav .theme-toggle {\r\n    margin-left: 1rem;\r\n}\r\n\r\n#desktop-nav .lang-toggle {\r\n    margin-left: 0.5rem;\r\n}\r\n\r\na {\r\n    color:rgb(0, 0, 0);\r\n    text-decoration: none;\r\n    text-decoration-color: white;\r\n}\r\n\r\na:hover {\r\n    color:grey;\r\n    text-decoration: underline;\r\n    text-underline-offset: 1rem;\r\n    text-decoration-color: rgb(181,181,181);\r\n}\r\n\r\n.logo{\r\n    font-size: 2rem;\r\n}\r\n\r\n.logo:hover {\r\n    cursor: default;\r\n}\r\n\r\n\r\n\r\n/*HAMBURGER MENU*/\r\n\r\n#hamburger-nav {\r\n    display: none;\r\n    z-index: 5;\r\n}\r\n\r\n.hamburger-menu {\r\n    position: relative;\r\n    display: inline-block;\r\n}\r\n\r\n.hamburger-icon{\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n    height: 24px;\r\n    width: 30px;\r\n    cursor: pointer;\r\n}\r\n\r\n.hamburger-icon span{\r\n    width: 100%;\r\n    height: 2px;\r\n    background-color: rgb(0, 0, 0);\r\n    transition: all 0.3 ease-in-out;\r\n}\r\n\r\n#hamburger-nav .theme-toggle {\r\n    position: absolute;\r\n    top: 1.5rem;\r\n    right: 1rem;\r\n}\r\n\r\n#hamburger-nav .lang-toggle {\r\n    position: absolute;\r\n    top: 4.5rem;\r\n    right: 1rem;\r\n}\r\n\r\n.menu-links{\r\n    position: absolute;\r\n    top: 100%;\r\n    right: 0;\r\n    background-color: white;\r\n    border-radius: 12px;\r\n    background: rgba(255,255,255,0.05);\r\n    transform: translateY(20px);\r\n    width: fit-content;\r\n    max-height: 0;\r\n    overflow: hidden;\r\n    transition: all 0.3 ease-in-out;\r\n    z-index: 10;\r\n}\r\n\r\n.menu-links a {\r\n    display: block;\r\n    padding: 10px;\r\n    text-align: center;\r\n    font-size: 1.5rem;\r\n    color: rgb(78, 65, 65);\r\n    text-decoration: none;\r\n    transition: all 0.3 ease-in-out;\r\n}\r\n\r\n.menu-links {\r\n    list-style: none;\r\n}\r\n\r\n.menu-links.open {\r\n    max-height: 300px;\r\n}\r\n\r\n.hamburger-icon.open span:first-child{\r\n    transform: rotate(45deg) translate(10px,5px);\r\n}\r\n\r\n.hamburger-icon.open span:nth-child(2){\r\n    opacity: 0;\r\n}\r\n\r\n.hamburger-icon.open span:last-child{\r\n    transform: rotate(-45deg) translate(10px,-5px);\r\n}\r\n\r\n.hamburger-icon span:first-child {\r\n    transform: none;\r\n}\r\n\r\n.hamburger-icon span:first-child {\r\n    opacity: 1;\r\n}\r\n.hamburger-icon span:first-child {\r\n    transform: none;\r\n}\r\n\r\n/* SECTIONS */\r\n\r\nsection {\r\n    padding-top: 4vh;\r\n    height: 96vh;\r\n    margin: 0 10rem;\r\n    box-sizing: border-box;\r\n    min-height: fit-content;\r\n}\r\n\r\n.section-container {\r\n    display: flex;\r\n}\r\n\r\n/* PROFILE SECTION */\r\n\r\n#profile {\r\n    display: flex;\r\n    justify-content: center;\r\n    gap: 5rem;\r\n    height: 80vh;\r\n}\r\n\r\n.section__pic-container {\r\n    display: flex;\r\n    height: 400px;\r\n    width: 400px;\r\n    margin: auto 0;\r\n    border-radius: 50%;\r\n    overflow:visible;\r\n}\r\n\r\n#profile-pic {\r\n    border-radius: 12px;\r\n}\r\n\r\n.section__text {\r\n    align-self: center;\r\n    text-align: center;\r\n}\r\n\r\n.section__text p{\r\n    font-weight: 600;\r\n}\r\n\r\n.section__text__p1{\r\n    text-align: center;\r\n}\r\n\r\n.section__text__p2{\r\n    font-size: 1.75rem;\r\n    margin-bottom: 1rem;\r\n}\r\n\r\n.title {\r\n    font-size:3rem;\r\n    text-align: center;\r\n}\r\n\r\n#socials-container {\r\n    display: flex;\r\n    justify-content: center;\r\n    margin-top: 1rem;\r\n    gap:1rem;\r\n}\r\n\r\n/* ICON */\r\n\r\n.icon {\r\n    cursor: pointer;\r\n    height: 2rem;\r\n}\r\n\r\n/* BUTTONS */\r\n\r\n.btn-container {\r\n    display: flex;\r\n    justify-content: center;\r\n    gap:1rem;\r\n}\r\n\r\n.btn{\r\n    font-weight: 600;\r\n    background: var(--btn-bg);\r\n    color: var(--btn-color);\r\n    transition: all 300 ms ease;\r\n    padding: 1rem;\r\n    width: 8rem;\r\n    border-radius: 2rem;\r\n}\r\n\r\n.btn-color-1, .btn-color-2, .btn-color-3 {\r\n    border:rgb(53, 53, 53) 0.1rem solid;\r\n}\r\n\r\n.btn-color-1:hover,\r\n.btn-color-2:hover,\r\n.btn-color-3:hover {\r\n    cursor: pointer;\r\n}\r\n\r\n.btn-color-1,\r\n.btn-color-2:hover,\r\n.btn-color-3:hover {\r\n    background: rgb(53, 53, 53);\r\n    color: white;\r\n}\r\n\r\n.btn-color-1:hover {\r\n    background: rgb(0, 0, 0);\r\n}\r\n\r\n.btn-color-2 {\r\n    background: none;\r\n}\r\n\r\n.btn-color-2:hover{\r\n    border: rgb(255,255,255) 0.1rem solid;\r\n}\r\n\r\n.btn-container {\r\n    gap: 1rem;\r\n}\r\n\r\n/* ABOUT SECTION */\r\n\r\n#about {\r\n    position: relative;\r\n}\r\n\r\n.about-containers {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    gap:2rem;\r\n    justify-content: center;\r\n}\r\n\r\n.about-details-container {\r\n    justify-content: center;\r\n    flex-direction: column;\r\n}\r\n\r\n.about-containers, .about-details-container {\r\n    display: flex;\r\n}\r\n\r\n.about-pic {\r\n    border-radius: 30%;\r\n}\r\n\r\n.arrow {\r\n    position: absolute;\r\n    right:-5rem;\r\n    bottom: 5rem;\r\n}\r\n\r\n#profile  .arrow {\r\n    position: absolute;\r\n    right:5rem;\r\n    bottom: 5rem;\r\n}\r\n\r\n.details-container {\r\n    padding: 1.5rem;\r\n    flex:1;\r\n    background: var(--section-bg);\r\n    border-radius: 2rem;\r\n    border:rgb(53, 53, 53) 0.1rem solid;\r\n    border-color: rgb(163,163,163);\r\n    text-align: center;\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.details-container:hover {\r\n    transform: translateY(-5px);\r\n    box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.details-container.color-container {\r\n    flex: 1 1 250px; \r\n    max-width: 350px; \r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    padding: 1rem;\r\n}\r\n\r\n.section-container {\r\n    gap:4rem;\r\n    height: 80%;\r\n}\r\n\r\n.section__pic-container{\r\n    height: 400px;\r\n    width: 400px;\r\n    margin:auto 0;\r\n    border-radius: 30%;\r\n}\r\n\r\n/* EXPERIENCE SECTION*/\r\n\r\n#experiance {\r\n    position: relative;\r\n}\r\n\r\n.experiance-sub-title{\r\n    color: rgb(85, 85, 85);\r\n    font-weight: 600;\r\n    font-size: 1.75rem;\r\n    margin-bottom: 2rem;\r\n}\r\n\r\n.experiance-details-container {\r