___CSS_LOADER_EXPORT___.push([module.id, `
@media screen and (max-width:1400px) {
    #profile{
        height:83vh;
        margin-bottom: 6rem;

    }
    .about-containers{
        flex-wrap:wrap;
    }

    
}

@media screen and (max-width:1200px) {
    #desktop-nav {
        display: none;
    }
    #hamburger-nav{
        display: flex;
    }
    #experience, 
    .experience-details-container{
        margin-top:2rem;
    }
    #profile,
    .section-container{
        display: block;
    }
    .arrow {
        display: none;
    }
    section,
    .section-container {
       height: fit-content; 
    }
    section{
        margin:0 5%;
    }
    .section__pic-container {
        width: 275px;
        height: 275px;
        margin:0 auto 2rem;
    }
    .about-containers{
        margin-top: 0;
        flex-wrap: wrap;
    }

    #desktop-nav .theme-toggle {
        display: none;
    }
    #hamburger-nav .theme-toggle {
        display: block;
    }

    #desktop-nav .lang-toggle {
        display: none;
    }
    #hamburger-nav .lang-toggle {
        display: block;
    }

    #hamburger-nav .theme-toggle {
    top: 0.5rem;
    right: 2rem;
  }

  #hamburger-nav .lang-toggle {
    top: 3.5rem;
    right: 2rem;
  }
}

@media screen and (min-width: 1201px) {
    #desktop-nav .theme-toggle {
        display: block;
    }
    #hamburger-nav .theme-toggle {
        display: none;
    }
    
}


@media screen and (max-width: 768px) {
  #desktop-nav {
    display: none;
  }

  #hamburger-nav {
    display: flex;
  }

 
  #hamburger-nav .theme-toggle,
  #hamburger-nav .lang-toggle {
    z-index: 20; 
  }

  
  .menu-links {
    z-index: 10;
  }

  
  #hamburger-nav .theme-toggle {
    top: 1rem;
    right: 8rem;
  }

  #hamburger-nav .lang-toggle {
    top: 4rem;
    right: 8rem;
  }
}


@media screen and (max-width:600px) {
    #contact,
    footer{
        height: 40vh;
    }
    #profile{
        height:83vh;
        margin-bottom: 0;
    }
    article{
        font-size: 1rem;
    }
    footer nav{
        height: fit-content;
        margin-bottom: 2rem;
    }

    .about-containers,
    .contact-info-upper-container,
    .btn-container{
        flex-wrap: wrap;
    }

    .contact-info-container {
        margin:0;
    }
    .contact-info-container p, 
    .nav-links li a{
        font-size: 1rem;
    }

    .experiance-sub-title {
        font-size: 1.25rem;
    }

    .logo{
        font-size: 1.2rem;
    }
    .nav-links{
        flex-direction: column;
        gap: 0.5rem;
        text-align: center;
    }

    .section__pic-container {
        width:auto;
        height: 46vw;
        justify-content: center;
    }

    .section__text__p2{
        font-size: 1.25rem;
    }

    .title{
        font-size: 2rem;
    }

    .text-container{
        text-align: justify;
    }

        #hamburger-nav .theme-toggle {
    top: 0.5rem;
    right: 7rem;
  }

  #hamburger-nav .lang-toggle {
    top: 3.5rem;
    right: 7rem;
  }

}


@media screen and (max-width:380px) {
   

    #hamburger-nav .theme-toggle {
    top: -0.5rem;
    right: 0rem;
  }

  #hamburger-nav .lang-toggle {
    top: 5rem;
    right: 0rem;
  }
